.card {
    display: flex;
    flex-direction: column;
    width: 330px;
    margin-bottom: 40px;

    &__media {
        position: relative;
        height: 240px;
        padding: 20px;
        margin-bottom: 10px;

        opacity: 0.5; }

    &:hover {
        .card__media {
            opacity: 1; }
        .card__angle:before, .card__angle:after {
            background-color: #f0c808; } }

    &__angle {
        position: absolute;
        top: 0;
        left: 0;

        &:before, &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 70px;
            height: 10px;
            background-color: #f1f2f7; }

        &:after {
            width: 10px;
            height: 70px; }

        &--bottom {
            top: auto;
            left: auto;
            right: 0;
            bottom: 0;
            &:before, &:after {
                top: auto;
                left: auto;
                right: 0;
                bottom: 0; } } }

    &__title {
        font-size: 24px;
        color: $blue;
        text-align: center;
        margin-bottom: 15px; }

    p {
        position: relative;
        display: flex;
        justify-content: space-around;
        color: #000000;

        &:after {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 1px;
            height: 18px;
            background-color: #f1f2f7; } }

    &__price, &__time {
        font-size: 18px;
        font-weight: 500; } }
