.nav {
    display: flex;
    justify-content: space-between;

    &__item {
        display: flex;
        border-right: 1px solid #eff2f7;
        flex-grow: 1;
        &:first-child {
            border-left: 1px solid #eff2f7; }
        &:hover {
            .nav__link {
                background-color: $blue;
                color: #ffffff;
                text-decoration: none; } } }

    &__link {
        padding: 12px 0;
        text-align: center;
        width: 100%;
        color: #6d86a0;
        text-decoration: underline; }
    @include r(1440) {
        &__link {
            padding: 12px 32px;
            font-size: 14px; }
        &__item {
            border: none;
            &:first-child {
                border: none; } } }


    @include r(1200) {
        display: none; } }


.nav-product {
    display: flex;
    padding: 18px 0;

    &__item {
        padding-left: 50px; }

    &__link {
        color: white;
        font-size: 20px;
        font-weight: 500;

        &:hover {
            text-decoration: underline; } }


    @include r(1440) {
        &__link {
            font-size: 18px; } }

    @include r(1200) {
        display: none; } }
