.header {

    &__in {
        display: flex;
        justify-content: space-between; }

    &__wrapper {
        display: flex; }

    &__center {
        padding: 50px 0;
        box-shadow: 5px 0px 15px rgba(0,0,0,0.2); }

    &__bottom {
        background-color: $blue;
        .language {
            display: none; }

        .logo {
            display: none; } }


    @include r(1200) {

        &__center {
            display: none; }
        &__bottom {
            display: block;
            .header__wrapper {
                display: flex;
                justify-content: space-between;
                align-items: center; }
            .language {
                display: flex;
                flex-direction: row;
                &__item {
                    margin-bottom: 0;
                    margin-right: 20px; } }

            .logo {
                display: block;
                background-color: white;
                padding: 10px;

                height: 60px;
                z-index: 5;
                img {
                    height: 100%; } } } } }



