html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

.icon-facebook { width: 0.53em; height: 1em; fill: #000; }

body { font-family: 'Ubuntu', sans-serif; font-size: 16px; line-height: 1.2; }

body.fixed { position: fixed; right: 0; left: 0; }

a { text-decoration: none; }

.container { width: 1410px; margin: 0 auto; }

@media only screen and (max-width: 1440px) { .container { width: 1200px; } }

@media only screen and (max-width: 1200px) { .container { width: 100%; padding: 0 20px; } }

.metro, .address, .phone, .tel, .mail { display: block; padding-left: 45px; }

.metro { font-size: 30px; font-weight: 700; color: #004f9f; background: transparent url("../img/metro.png") left center no-repeat; margin-bottom: 10px; }

@media only screen and (max-width: 1440px) { .metro { font-size: 24px; } }

.address { font-size: 16px; line-height: 36px; background: transparent url("../img/address.png") left center no-repeat; }

@media only screen and (max-width: 1440px) { .address { font-size: 14px; } }

.phone { font-size: 30px; font-weight: 700; color: #004f9f; background: transparent url("../img/phone.png") left center no-repeat; margin-bottom: 10px; }

.phone:hover { text-decoration: underline; }

@media only screen and (max-width: 1440px) { .phone { font-size: 24px; } }

.tel { font-size: 16px; line-height: 36px; color: #000000; background: transparent url("../img/tel.png") left center no-repeat; margin-right: 20px; }

.tel:hover { text-decoration: underline; }

@media only screen and (max-width: 1440px) { .tel { font-size: 14px; } }

.mail { font-size: 16px; line-height: 36px; color: #004f9f; text-decoration: underline; background: transparent url("../img/mail.png") left center no-repeat; }

.mail:hover { text-decoration: none; }

@media only screen and (max-width: 1440px) { .mail { font-size: 14px; } }

.block { display: flex; }

.burger { display: flex; flex-direction: column; justify-content: space-between; width: 90px; min-height: 60px; padding: 15px 20px; background-color: #054281; z-index: 5; }

.burger span { height: 5px; width: 100%; background-color: white; }

.burger--mobile { display: none; }

.burger.is-active { background-color: #004f9f; }

.burger.is-active span:nth-child(1) { transform: translateY(13px) rotate(45deg); }

.burger.is-active span:nth-child(2) { transform: translateX(100%); opacity: 0; visibility: hidden; }

.burger.is-active span:nth-child(3) { transform: translateY(-12px) rotate(-45deg); }

@media only screen and (max-width: 1200px) { .burger { display: none; }
  .burger--mobile { display: flex; } }

.hero { margin-bottom: 110px; box-shadow: 0 1px 10px rgba(0, 0, 0, 0.2); }

.title { position: relative; padding: 5px 0 5px 33px; margin-bottom: 58px; }

.title:before { content: ''; position: absolute; top: 0; left: 0; display: block; width: 8px; height: 100%; background-color: #70a513; }

.title h2 { font-size: 46px; margin-bottom: 5px; letter-spacing: -2px; }

.title p { font-size: 18px; max-width: 760px; }

.title--btn { display: flex; justify-content: space-between; align-items: center; }

@media only screen and (max-width: 1440px) { .title h2 { font-size: 40px; } }

@media only screen and (max-width: 1200px) { .title--btn { flex-direction: column; }
