@media only screen and (max-width: 768px) { .title h2 { font-size: 30px; } }

.our-client { position: relative; padding-bottom: 160px; }

.our-client:before { content: ''; position: absolute; bottom: 50px; left: 50%; transform: translateX(-50%); display: block; width: 100%; height: 50px; border-top: 1px solid #e8e8e8; background: transparent url("../img/shadow.png") top center no-repeat; }

.btn { display: flex; font-size: 20px; color: white; padding: 12px 60px; background-color: #ce1629; }

.btn:hover { box-shadow: 0 2px 15px 0px rgba(0, 0, 0, 0.2); opacity: 0.9; }

@media only screen and (max-width: 1440px) { .btn { padding: 12px 50px; } }

@media only screen and (max-width: 768px) { .btn { font-size: 16px; } }

.polygraphy { margin-bottom: 70px; box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.1); }

.polygraphy__content { display: flex; justify-content: space-between; flex-wrap: wrap; }

@media only screen and (max-width: 1200px) { .polygraphy__content { justify-content: space-around; } }

@media only screen and (max-width: 768px) { .polygraphy__content { justify-content: center; } }

.features { position: relative; padding-bottom: 160px; }

.features:before { content: ''; position: absolute; bottom: 50px; left: 50%; transform: translateX(-50%); display: block; width: 100%; height: 50px; border-top: 1px solid #e8e8e8; background: transparent url("../img/shadow.png") top center no-repeat; }

.features__wrapper { display: flex; justify-content: space-between; }

.features__item { position: relative; width: 330px; padding: 30px; }

.features__item--red h4 { color: #fe5f55; }

.features__item--red .features__angle:before, .features__item--red .features__angle:after { background-color: #fe5f55; }

.features__item--green h4 { color: #70a513; }

.features__item--green .features__angle:before, .features__item--green .features__angle:after { background-color: #70a513; }

.features__item--blue h4 { color: #06aed5; }

.features__item--blue .features__angle:before, .features__item--blue .features__angle:after { background-color: #06aed5; }

.features__item--yellow h4 { color: #f0c808; }

.features__item--yellow .features__angle:before, .features__item--yellow .features__angle:after { background-color: #f0c808; }

.features__angle { position: absolute; top: 0; left: 0; }

.features__angle:before, .features__angle:after { content: ''; position: absolute; top: 0; left: 0; width: 70px; height: 10px; }

.features__angle:after { width: 10px; height: 70px; }

.features__angle--bottom { top: auto; left: auto; right: 0; bottom: 0; }

.features__angle--bottom:before, .features__angle--bottom:after { top: auto; left: auto; right: 0; bottom: 0; }

.features h4 { font-size: 64px; font-weight: 700; margin-bottom: 10px; text-align: center; }

.features p { font-size: 18px; font-weight: 500; color: #004f9f; text-align: center; }

@media only screen and (max-width: 1440px) { .features__item { width: 270px; }
  .features h4 { font-size: 54px; }
  .features p { font-size: 16px; } }

@media only screen and (max-width: 1200px) { .features__wrapper { flex-wrap: wrap; justify-content: space-around; }
  .features__item { width: 34%; } }

@media only screen and (max-width: 768px) { .features__wrapper { flex-direction: column; justify-content: center; align-items: center; }
  .features__item { width: 100%; max-width: 330px; margin-bottom: 20px; } }

.mobile-menu { display: none; position: fixed; top: 0; left: 0; right: 0; bottom: 0; padding: 100px 30px 40px; background-color: #004f9f; z-index: 4; }

.mobile-menu.is-show { display: block; }

.mobile-menu .nav-product, .mobile-menu .nav { display: flex; flex-direction: column; }

.mobile-menu .nav-product__item, .mobile-menu .nav-product__link, .mobile-menu .nav__item, .mobile-menu .nav__link { margin: 0; padding: 0; }

.mobile-menu .nav-product__item, .mobile-menu .nav__item { justify-content: center; align-items: center; margin-bottom: 15px; }

.mobile-menu .nav-product__link, .mobile-menu .nav__link { display: block; text-align: center; text-decoration: none; font-size: 20px; font-weight: 500; color: white; text-align: center; }

.header__in { display: flex; justify-content: space-between; }

.header__wrapper { display: flex; }

.header__center { padding: 50px 0; box-shadow: 5px 0px 15px rgba(0, 0, 0, 0.2); }

.header__bottom { background-color: #004f9f; }

.header__bottom .language { display: none; }

.header__bottom .logo { display: none; }

@media only screen and (max-width: 1200px) { .header__center { display: none; }
  .header__bottom { display: block; }
  .header__bottom .header__wrapper { display: flex; justify-content: space-between; align-items: center; }
  .header__bottom .language { display: flex; flex-direction: row; }
  .header__bottom .language__item { margin-bottom: 0; margin-right: 20px; }
  .header__bottom .logo { display: block; background-color: white; padding: 10px; height: 60px; z-index: 5; }
  .header__bottom .logo img { height: 100%; } }

.nav { display: flex; justify-content: space-between; }

.nav__item { display: flex; border-right: 1px solid #eff2f7; flex-grow: 1; }

.nav__item:first-child { border-left: 1px solid #eff2f7; }

.nav__item:hover .nav__link { background-color: #004f9f; color: #ffffff; text-decoration: none; }

.nav__link { padding: 12px 0; text-align: center; width: 100%; color: #6d86a0; text-decoration: underline; }

@media only screen and (max-width: 1440px) { .nav__link { padding: 12px 32px; font-size: 14px; }
  .nav__item { border: none; }
  .nav__item:first-child { border: none; } }

@media only screen and (max-width: 1200px) { .nav { display: none; } }

.nav-product { display: flex; padding: 18px 0; }

.nav-product__item { padding-left: 50px; }

.nav-product__link { color: white; font-size: 20px; font-weight: 500; }

.nav-product__link:hover { text-decoration: underline; }

@media only screen and (max-width: 1440px) { .nav-product__link { font-size: 18px; } }

@media only screen and (max-width: 1200px) { .nav-product { display: none; } }

.language { display: flex; flex-direction: column; z-index: 5; }

.language__item { display: flex; flex-direction: column; justify-content: flex-end; width: 34px; height: 34px; margin-bottom: 15px; overflow: hidden; opacity: 0.5; font-size: 0; border: 4px solid #f8f8f8; border-radius: 50%; }

.language__item:last-child { margin-bottom: 0; }

.language__item.is-active { border: 4px solid #f0f0f0; opacity: 1; }

.language__item[data-lang="ua"]:after, .language__item[data-lang="ua"]:before { content: ''; display: block; width: 100%; height: 50%; background-color: #ffd500; }

.language__item[data-lang="ua"]:before { background-color: #005bbb; }

.language__item[data-lang="ru"] { background-color: white; }

.language__item[data-lang="ru"]:after, .language__item[data-lang="ru"]:before { content: ''; display: block; width: 100%; height: 33%; background-color: #0039a6; }

.language__item[data-lang="ru"]:before { background-color: #d52b1e; }

.slider { position: relative; height: 500px; }

.slider .slick-arrow { position: absolute; top: 50%; transform: translateY(-50%); left: 40px; width: 80px; height: 80px; font-size: 0; background-color: #ffffff; box-shadow: 0 2px 15px 0px rgba(0, 0, 0, 0.2); z-index: 1; }

.slider .slick-next { left: auto; right: 40px; }

.slider .slick-arrow:before, .slider .slick-arrow:after { content: ''; position: absolute; top: 34px; left: 36px; display: block; width: 5px; height: 34px; background-color: #004f9f; transform: rotate(45deg); border-radius: 1px; }

.slider .slick-arrow:hover { background-color: #004f9f; }

.slider .slick-arrow:hover::before, .slider .slick-arrow:hover::after { background-color: #ffffff; }

.slider .slick-next:before, .slider .slick-next:after { right: 32px; left: auto; }

.slider .slick-arrow:after { transform: rotate(-45deg); top: 14px; }

.slider .slick-prev:after { transform: rotate(-135deg); }

.slider .slick-prev:before { transform: rotate(135deg); }

@media only screen and (max-width: 1440px) { .slider { height: 400px; }
  .slider .slick-arrow { transform: scale(0.6) translateY(-50%); } }

@media only screen and (max-width: 768px) { .slider { height: 300px; } }

.slide { position: relative; }

.slide__preview { position: absolute; top: 80px; left: 270px; }

.slide__preview p:first-child { font-size: 38px; color: #004f9f; }

.slide__preview p:first-child span { color: #ce1629; }

.slide__preview p:nth-child(2) { font-size: 80px; font-weight: 500; color: #004f9f; margin-bottom: 15px; }

.slide__preview p:last-child { font-size: 26px; color: white; padding: 6px 0; text-align: center; background-color: #ce1629; }

.slide__img { display: flex; height: 500px; }

.slide__img img { height: 100%; }

@media only screen and (max-width: 1440px) { .slide__img { height: 400px; }
  .slide__preview { left: 200px; } }

@media only screen and (max-width: 1200px) { .slide__preview { left: 160px; } }

@media only screen and (max-width: 768px) { .slide__preview { width: 100%; max-width: 390px; padding: 20px; top: 40px; left: 50%; transform: translateX(-50%); }
  .slide__preview p:first-child { font-size: 28px; }
  .slide__preview p:first-child span { color: #ce1629; }
  .slide__preview p:nth-child(2) { font-size: 60px; font-weight: 500; color: #004f9f; margin-bottom: 15px; letter-spacing: -1px; }
  .slide__preview p:last-child { font-size: 18px; }
  .slide__img { height: 300px; } }

.slider-small { position: relative; }

.slider-small .slick-arrow { position: absolute; top: 50%; transform: translateY(-50%); left: 0; width: 40px; height: 40px; font-size: 0; background-color: #ffffff; box-shadow: 0 2px 15px 0px rgba(0, 0, 0, 0.2); z-index: 1; }

.slider-small .slick-next { left: auto; right: 0; }

.slider-small .slick-arrow:before, .slider-small .slick-arrow:after { content: ''; position: absolute; top: 15px; left: 17px; display: block; width: 3px; height: 18px; background-color: #004f9f; transform: rotate(45deg); border-radius: 1px; }

.slider-small .slick-arrow:hover { background-color: #004f9f; }

.slider-small .slick-arrow:hover::before, .slider-small .slick-arrow:hover::after { background-color: #ffffff; }

.slider-small .slick-next:before, .slider-small .slick-next:after { right: 17px; left: auto; }

.slider-small .slick-arrow:after { transform: rotate(-45deg); top: 4px; }

.slider-small .slick-prev:after { transform: rotate(-135deg); }

.slider-small .slick-prev:before { transform: rotate(135deg); }

.slider-small__item { padding: 0 85px; }

@media only screen and (max-width: 1440px) { .slider-small__item { padding: 0 50px; } }

@media only screen and (max-width: 768px) { .slider-small img { margin: 0 auto; } }

.technologies__wrapper { display: flex; justify-content: space-between; }

.technologies__items { display: flex; justify-content: space-between; flex-wrap: wrap; }

.technologies__item { width: 210px; margin-right: 30px; margin-bottom: 30px; }

.technologies__item img { width: 100%; object-fit: cover; margin-bottom: 5px; }

.technologies__item h5 { font-size: 20px; font-weight: 700; color: #004f9f; text-align: center; }

.technologies__item:hover h5 { text-decoration: underline; }

.technologies__preview { flex-shrink: 0; width: 450px; padding: 28px; margin-bottom: 40px; box-shadow: 0 2px 15px 0px rgba(0, 0, 0, 0.2); }

.technologies__preview img { margin: 0 auto; max-width: 100%; }

.technologies__preview h5 { font-size: 20px; font-weight: 700; color: #004f9f; margin-bottom: 30px; }

.technologies__preview p { font-size: 18px; }

.technologies__media { margin-bottom: 20px; }

@media only screen and (max-width: 1440px) { .technologies__preview { width: 400px; }
  .technologies__preview h5 { margin-bottom: 15px; }
  .technologies__preview p { font-size: 16px; }
  .technologies__item { width: 170px; } }

@media only screen and (max-width: 1200px) { .technologies__wrapper { flex-direction: column; }
  .technologies__items { justify-content: space-around; }
  .technologies__media { display: flex; }
  .technologies__preview { width: 100%; } }

.card { display: flex; flex-direction: column; width: 330px; margin-bottom: 40px; }

.card__media { position: relative; height: 240px; padding: 20px; margin-bottom: 10px; opacity: 0.5; }

.card:hover .card__media { opacity: 1; }

.card:hover .card__angle:before, .card:hover .card__angle:after { background-color: #f0c808; }

.card__angle { position: absolute; top: 0; left: 0; }

.card__angle:before, .card__angle:after { content: ''; position: absolute; top: 0; left: 0; width: 70px; height: 10px; background-color: #f1f2f7; }

.card__angle:after { width: 10px; height: 70px; }

.card__angle--bottom { top: auto; left: auto; right: 0; bottom: 0; }

.card__angle--bottom:before, .card__angle--bottom:after { top: auto; left: auto; right: 0; bottom: 0; }

.card__title { font-size: 24px; color: #004f9f; text-align: center; margin-bottom: 15px; }

.card p { position: relative; display: flex; justify-content: space-around; color: #000000; }

.card p:after { content: ''; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 1px; height: 18px; background-color: #f1f2f7; }

.card__price, .card__time { font-size: 18px; font-weight: 500; }

/* Slider */
.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }
