.language {
    display: flex;
    flex-direction: column;
    z-index: 5;

    &__item {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;

        width: 34px;
        height: 34px;

        margin-bottom: 15px;
        &:last-child {
            margin-bottom: 0; }

        overflow: hidden;
        opacity: 0.5;
        font-size: 0;
        border: 4px solid #f8f8f8;
        border-radius: 50%;

        &.is-active {
            border: 4px solid #f0f0f0;
            opacity: 1; }



        &[data-lang="ua"] {
            &:after, &:before {
                content: '';
                display: block;
                width: 100%;
                height: 50%;
                background-color: #ffd500; }

            &:before {
                background-color: #005bbb; } }

        &[data-lang="ru"] {
            background-color: white;
            &:after, &:before {
                content: '';
                display: block;
                width: 100%;
                height: 33%;
                background-color: #0039a6; }

            &:before {
                background-color: #d52b1e; } } } }
