body {
    font-family: 'Ubuntu', sans-serif;
    font-size: 16px;
    line-height: 1.2;
    // letter-spacing: -1px

    &.fixed {
        position: fixed;
        right: 0;
        left: 0; } }

a {
    text-decoration: none; }

.container {
    width: 1410px;
    margin: 0 auto;
    @include r(1440) {
        width: 1200px; }

    @include r(1200) {
        width: 100%;
        padding: 0 20px; } }

.metro, .address, .phone, .tel, .mail {
    display: block;
    padding-left: 45px; }

.metro {
    font-size: 30px;
    font-weight: 700;
    color: $blue;
    background: transparent url("../img/metro.png") left center no-repeat;
    margin-bottom: 10px;
    @include r(1440) {
        font-size: 24px; } }

.address {
    font-size: 16px;
    line-height: 36px;
    background: transparent url("../img/address.png") left center no-repeat;
    @include r(1440) {
        font-size: 14px; } }


.phone {
    font-size: 30px;
    font-weight: 700;
    color: $blue;
    background: transparent url("../img/phone.png") left center no-repeat;
    margin-bottom: 10px;
    &:hover {
        text-decoration: underline; }
    @include r(1440) {
        font-size: 24px; } }

.tel {
    font-size: 16px;
    line-height: 36px;
    color: #000000;
    background: transparent url("../img/tel.png") left center no-repeat;
    margin-right: 20px;
    &:hover {
        text-decoration: underline; }
    @include r(1440) {
        font-size: 14px; } }

.mail {
    font-size: 16px;
    line-height: 36px;
    color: $blue;
    text-decoration: underline;
    background: transparent url("../img/mail.png") left center no-repeat;
    &:hover {
        text-decoration: none; }
    @include r(1440) {
        font-size: 14px; } }

.block {
    display: flex; }

.burger {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 90px;
    min-height: 60px;
    padding: 15px 20px;
    background-color: #054281;
    z-index: 5;
    span {
        height: 5px;
        width: 100%;
        background-color: white; }

    &--mobile {
        display: none; }

    &.is-active {
        background-color: $blue;
        span {
            &:nth-child(1) {
                transform: translateY(13px) rotate(45deg); }

            &:nth-child(2) {
                transform: translateX(100%);
                opacity: 0;
                visibility: hidden; }

            &:nth-child(3) {
                transform: translateY(-12px) rotate(-45deg); } } }

    @include r(1200) {
        display: none;
        &--mobile {
            display: flex; } } }

.hero {
    margin-bottom: 110px;
    box-shadow: 0 1px 10px rgba(0,0,0,0.2); }

.title {
    position: relative;
    padding: 5px 0 5px 33px;
    margin-bottom: 58px;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 8px;
        height: 100%;
        background-color: #70a513; }

    h2 {
        font-size: 46px;
        margin-bottom: 5px;
        letter-spacing: -2px; }

    p {
        font-size: 18px;
        max-width: 760px; }
    &--btn {
        display: flex;
        justify-content: space-between;
        align-items: center; }

    @include r(1440) {
        h2 {
            font-size: 40px; } }

    @include r(1200) {
        &--btn {
            flex-direction: column;
            .btn {
                margin-top: 20px; } } }
    @include r(768) {
        h2 {
            font-size: 30px; } } }

.our-client {
    position: relative;
    padding-bottom: 160px;

    &:before {
        content: '';
        position: absolute;
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        width: 100%;
        height: 50px;
        border-top: 1px solid #e8e8e8;
        background: transparent url('../img/shadow.png') top center no-repeat; } }

.btn {
    display: flex;
    font-size: 20px;
    color: white;
    padding: 12px 60px;
    background-color: #ce1629;

    &:hover {
        box-shadow: 0 2px 15px 0px rgba(0,0,0,0.2);
        opacity: 0.9; }

    @include r(1440) {
        padding: 12px 50px; }
    @include r(768) {
        font-size: 16px; } }




.polygraphy {
    margin-bottom: 70px;
    box-shadow: 0 4px 5px 0 rgba(0,0,0,0.1);
    &__content {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; }

    @include r(1200) {
        &__content {
            justify-content: space-around; } }
    @include r(768) {
        &__content {
            justify-content: center; } } }


.features {
    position: relative;
    padding-bottom: 160px;

    &:before {
        content: '';
        position: absolute;
        bottom: 50px;
        left: 50%;
        transform: translateX(-50%);
        display: block;
        width: 100%;
        height: 50px;
        border-top: 1px solid #e8e8e8;
        background: transparent url('../img/shadow.png') top center no-repeat; }


    &__wrapper {
        display: flex;
        justify-content: space-between; }

    &__item {
        position: relative;
        width: 330px;
        padding: 30px;
        &--red {
            h4 {
                color: #fe5f55; }
            .features__angle:before, .features__angle:after {
                background-color: #fe5f55; } }

        &--green {
            h4 {
                color: #70a513; }
            .features__angle:before, .features__angle:after {
                background-color: #70a513; } }

        &--blue {
            h4 {
                color: #06aed5; }
            .features__angle:before, .features__angle:after {
                background-color: #06aed5; } }

        &--yellow {
            h4 {
                color: #f0c808; }
            .features__angle:before, .features__angle:after {
                background-color: #f0c808; } } }

    &__angle {
        position: absolute;
        top: 0;
        left: 0;

        &:before, &:after {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 70px;
            height: 10px; }

        &:after {
            width: 10px;
            height: 70px; }

        &--bottom {
            top: auto;
            left: auto;
            right: 0;
            bottom: 0;
            &:before, &:after {
                top: auto;
                left: auto;
                right: 0;
                bottom: 0; } } }


    h4 {
        font-size: 64px;
        font-weight: 700;
        margin-bottom: 10px;
        text-align: center; }

    p {
        font-size: 18px;
        font-weight: 500;
        color: $blue;
        text-align: center; }
    @include r(1440) {
        &__item {
            width: 270px; }

        h4 {
            font-size: 54px; }

        p {
            font-size: 16px; } }

    @include r(1200) {
        &__wrapper {
           flex-wrap: wrap;
           justify-content: space-around; }
        &__item {
            width: 34%; } }
    @include r(768) {
        &__wrapper {
           flex-direction: column;
           justify-content: center;
           align-items: center; }
        &__item {
            width: 100%;
            max-width: 330px;
            margin-bottom: 20px; } } }

.mobile-menu {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 100px 30px 40px;
    background-color: $blue;
    z-index: 4;
    &.is-show {
        display: block; }

    .nav-product, .nav {
        display: flex;
        flex-direction: column;

        &__item, &__link {
            margin: 0;
            padding: 0; }
        &__item {
            justify-content: center;
            align-items: center;
            margin-bottom: 15px; }

        &__link {
            display: block;
            text-align: center;
            text-decoration: none;
            font-size: 20px;
            font-weight: 500;
            color: white;
            text-align: center; } } }

