.slider {
    position: relative;
    height: 500px;
    .slick-arrow {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 40px;

        width: 80px;
        height: 80px;

        font-size: 0;

        background-color: #ffffff;
        box-shadow: 0 2px 15px 0px rgba(0,0,0,0.2);

        z-index: 1; }

    .slick-next {
        left: auto;
        right: 40px; }

    & .slick-arrow:before, & .slick-arrow:after {
        content: '';
        position: absolute;
        top: 34px;
        left: 36px;

        display: block;
        width: 5px;
        height: 34px;
        background-color: $blue;
        transform: rotate(45deg);

        border-radius: 1px; }

    & .slick-arrow:hover {
        background-color: $blue; }
    & .slick-arrow:hover::before, & .slick-arrow:hover::after {
        background-color: #ffffff; }

    & .slick-next:before, & .slick-next:after {
        right: 32px;
        left: auto; }

    & .slick-arrow:after {
        transform: rotate(-45deg);
        top: 14px; }

    & .slick-prev:after {
        transform: rotate(-135deg); }

    & .slick-prev:before {
        transform: rotate(135deg); }

    @include r(1440) {
        height: 400px;
        .slick-arrow {
            transform: scale(0.6) translateY(-50%); } }

    @include r(768) {
        height: 300px; } }

.slide {
    position: relative;
    &__preview {
        position: absolute;
        top: 80px;
        left: 270px;
        p:first-child {
            font-size: 38px;
            color: $blue;
            span {
                color: #ce1629; } }
        p:nth-child(2) {
            font-size: 80px;
            font-weight: 500;
            color: $blue;
            margin-bottom: 15px; }
        p:last-child {
            font-size: 26px;
            color: white;
            padding: 6px 0;
            text-align: center;
            background-color: #ce1629; } }

    &__img {
        display: flex;
        height: 500px;
        img {
         height: 100%; } }
    @include r(1440) {
         &__img {
            height: 400px; }

         &__preview {
            left: 200px; } }

    @include r(1200) {
         &__preview {
            left: 160px; } }

    @include r(768) {
        &__preview {
            width: 100%;
            max-width: 390px;
            padding: 20px;
            top: 40px;
            left: 50%;
            transform: translateX(-50%);
            p:first-child {
                font-size: 28px;
                span {
                    color: #ce1629; } }
            p:nth-child(2) {
                font-size: 60px;
                font-weight: 500;
                color: $blue;
                margin-bottom: 15px;
                letter-spacing: -1px; }
            p:last-child {
                font-size: 18px; } }

        &__img {
            height: 300px; } } }




.slider-small {
    position: relative;

    .slick-arrow {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;

        width: 40px;
        height: 40px;

        font-size: 0;

        background-color: #ffffff;
        box-shadow: 0 2px 15px 0px rgba(0,0,0,0.2);

        z-index: 1; }

    .slick-next {
        left: auto;
        right: 0; }

    & .slick-arrow:before, & .slick-arrow:after {
        content: '';
        position: absolute;
        top: 15px;
        left: 17px;

        display: block;
        width: 3px;
        height: 18px;
        background-color: $blue;
        transform: rotate(45deg);

        border-radius: 1px; }

    & .slick-arrow:hover {
        background-color: $blue; }
    & .slick-arrow:hover::before, & .slick-arrow:hover::after {
        background-color: #ffffff; }

    & .slick-next:before, & .slick-next:after {
        right: 17px;
        left: auto; }

    & .slick-arrow:after {
        transform: rotate(-45deg);
        top: 4px; }

    & .slick-prev:after {
        transform: rotate(-135deg); }

    & .slick-prev:before {
        transform: rotate(135deg); }

    &__item {
        padding: 0 85px; }

    @include r(1440) {
        &__item {
            padding: 0 50px; } }

    @include r(768) {
        img {
            margin: 0 auto; } } }
