.technologies {

    &__wrapper {
        display: flex;
        justify-content: space-between; }

    &__items {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap; }


    &__item {
        width: 210px;
        margin-right: 30px;
        margin-bottom: 30px;
        img {
            width: 100%;
            object-fit: cover;
            margin-bottom: 5px; }
        h5 {
            font-size: 20px;
            font-weight: 700;
            color: $blue;
            text-align: center; }

        &:hover {
            h5 {
                text-decoration: underline; } } }

    &__preview {
        flex-shrink: 0;
        width: 450px;
        padding: 28px;
        margin-bottom: 40px;
        box-shadow: 0 2px 15px 0px rgba(0,0,0,0.2);


        img {
            margin: 0 auto;
            max-width: 100%; }
        h5 {
            font-size: 20px;
            font-weight: 700;
            color: $blue;
            margin-bottom: 30px; }
        p {
            font-size: 18px; } }

    &__media {
        margin-bottom: 20px; }

    @include r(1440) {
        &__preview {
            width: 400px;
            h5 {
                margin-bottom: 15px; }
            p {
                font-size: 16px; } }

        &__item {
            width: 170px; } }

    @include r(1200) {
        &__wrapper {
            flex-direction: column; }

        &__items {
            justify-content: space-around; }

        &__media {
            display: flex; }

        &__preview {
            width: 100%; } }


    @include r(768) {
        &__preview {} } }

